				tabEachOne: function() {
					var This=this;
					var myChart1 = echarts.init(document.getElementById('tabEachOneId'));
					var myChart2 = echarts.init(document.getElementById('tabEachTwoId'));
					var myChart3 = echarts.init(document.getElementById('tabEachThreeId'));

					// 指定图表的配置项和数据
					function eachZong(obj, obj2,min,max) {
						var option = $.extend(true, option, This.eachData);

						option.series = [{
							name: '',
							type: 'line',
							stack: '总量',
							data: []
						}]
						option.legend.data = [''];
						option.xAxis.data=[];
						option.series[0].data=[];
						
						for(var i = 0, j = obj.length - 1; i < obj.length; i++, j--) {
							
							var changeTime=obj[j].indexDate;
							var arrTime=changeTime.split('-');
							changeTime=arrTime[1]+'/'+arrTime[2];
							
							option.xAxis.data.push(changeTime);    //  x 轴
							option.series[0].data.push(obj[j].smeiValue);    // y 轴
						}
						option.yAxis.min=min;
						option.yAxis.max=max;
						obj2.setOption(option);
					}
					// 根据数据  生成 图表
					indexRrData({}).then((res) => {
					
						this.text = [];
						for(var i = 0; i < res.datas.length; i++) {
							var obj = {};
							obj.indexName = res.datas[i].indexName;
							obj.smeiValue = res.datas[i].priceList[0].smeiValue;
							obj.riseFall = res.datas[i].priceList[0].riseFall;
							obj.indexDate = res.datas[i].priceList[0].indexDate;
							this.text.push(obj);
						}

						eachZong(res.datas[0].priceList, myChart1,res.datas[0].min,res.datas[0].max);
						eachZong(res.datas[1].priceList, myChart2,res.datas[1].min,res.datas[1].max);
						eachZong(res.datas[2].priceList, myChart3,res.datas[2].min,res.datas[2].max);
					})
				},
				//brent 价格预测
				tabEachTwo: function() {
					var text='';
					var subtext1='';
					var subtext2='';
					var myChartone = echarts.init(document.getElementById('tabEachOne'));
					var option = $.extend(true, option, this.eachData);
					
					
					// 根据数据  生成 图表
					priceBrent({}).then((res) => {
						
						option.series[2].itemStyle = {
							 normal:{
			                    lineStyle:{
			                        width:2,
			                        type:'dotted'  //'dotted'虚线 'solid'实线
			                    }
			                }
						};
						option.tooltip = {
								trigger : 'axis',
								formatter : function(params) {
									var text = params[0].name;
									var value = params[0].data;
									if (value) {
										text += '<br />预测值：' + value;
									}
									if (params.length > 1) {
										var predict = params[1].data;
										if (predict) {
											text += '<br />实际值：' + predict;
										}
									}
									return text;
								}
						};
						
						 
						for(var i = 0; i < res.data.length; i++) {
							var obj = res.data[i];
							option.xAxis.data.push(obj.date);
							if(i<res.data.length-1){
								option.series[0].data.push(res.data[i].predict);   //predict
								option.series[1].data.push(res.data[i].value);     //value
							}
							
							if(i<res.data.length-2){
								option.series[2].data.push(null);
							}else{
								option.series[2].data.push(res.data[i].predict);
							}	
						}
						
						// console.log(Math.min(option.series[1].data[0] ))
						// console.log(Math.min(option.series[2].data[0] ))
						// console.log("数据集",option.series);
						
						var minArray = [];
						minArray.push(Math.min(option.series[0].data[0]));
						minArray.push(Math.min(option.series[1].data[0]));
						var mins1 = (Math.min(minArray[0])-5).toFixed(0);
						
						var lastObj1 = res.data[res.data.length-1];
						var lastObj2 = res.data[res.data.length-2];
						text = lastObj1.predict;
						subtext1 = (lastObj1.predict-lastObj2.value).toFixed(2);
						subtext2 = (subtext1/lastObj2.value*100).toFixed(2);
						option.legend.data.push('预测值','实际值');
//						option.legend.bottom=0;
//						option.grid.bottom=30;
						option.series[0].name='预测值'
						option.series[2].name='预测值'
						option.series[1].name='实际值'
						option.title= {
				            text: text,
				            subtext: subtext1+'  '+subtext2+'%',
				            left: 'right',
				            subtextStyle: {
				            	color: '#FF3034'
				            }
						};
						
						option.yAxis={
							min:mins1
						}
//						$('#tabEachOne').css('width', $('.priceWidth').innerWidth())
						
						myChartone.setOption(option);
					})
				},
