					 		categoryData.push(o.date.toString().substr(0,4)+'-' + o.date.toString().substr(4,2) + '-' + o.date.toString().substr(6,2));
							volumes.push(o.vol)	
							
					 	})
					 	option12.xAxis[0].data= categoryData;
				 		option12.xAxis[1].data= categoryData;
				 		option12.series[1].data = volumes;
						option12.series[0].data = valuess;
						
						myChart.setOption(option12); 
					 	
					})
				},
				tabBu(){
					this.num=0
				},
				tabW(){
					this.num=1
				},
				getEacOne(){
					this.priceTip=0
				},
				getEacTwo(){
