		  var mySwiper1 = new Swiper ('#slide_1', {
		    initialSlide :0,          //初始化索引
		    slidesPerView: 'auto',
		    spaceBetween: 0,
		    navigation: {
		      prevEl: '.last_1',
		      nextEl: '.next_1',
		    },
		  });
		
		  var mySwiper2 = new Swiper ('#slide_2', {
		    initialSlide :0,          //初始化索引
		    slidesPerView: 'auto',
		    spaceBetween: 0,
		    navigation: {
		      prevEl: '.last_2',
		      nextEl: '.next_2',
		    },
		  });
		
			var mySwiper1 = new Swiper ('#slide_3', {
		    initialSlide :0,          //初始化索引
		    slidesPerView: 'auto',
		    spaceBetween: 0,
		    navigation: {
		      prevEl: '.last_3',
		      nextEl: '.next_3',
		    },
		  });
		  $("#slide_1 .swiper-slide").on("click", function(e) {
		    $(this).parent().find("a").removeClass("cur");
		    $(this).find("a").addClass("cur");
		  });
		
		  $("#slide_2 .swiper-slide").on("click", function(e) {
		    $(this).parent().find("a").removeClass("cur");
		    $(this).find("a").addClass("cur");
		  });
		  
		  $("#slide_3 .swiper-slide").on("click", function(e) {
		    $(this).parent().find("a").removeClass("cur");
		    $(this).find("a").addClass("cur");
		  });
			
			
		}
	}
</script>

